<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>

		<b-form>

			<b-form-group label="*Fecha" label-for="fecharealizacion">
				<b-form-input id="fecharealizacion"
							  v-model="compras.fecharealizacion"
							  type="date"
							  placeholder="Ingrese la fecha de realizacion de la compra"
							  invalid-feedback="Complete este campo"
							  :state="validacion.fecharealizacion.estado"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="fecharealizacion-live-feedback">
					{{ validacion.fecharealizacion.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Tipo de Compra"
						  label-for="tipo_compra"
						  @submit.stop.prevent="handleSubmit">
				<b-form-select id="tipo_compra"
							   v-model="compras.tipo_compra"
							   type="text"
							   @change="cambiodetipo()"
							   placeholder="Describa el tipo de producto"
							   invalid-feedback="Complete este campo"
							   :state="validacion.tipo_compra.estado"
							   required
							   :options="tipos_productos">
				</b-form-select>
				<b-form-invalid-feedback id="tipo_compra-live-feedback">
					{{ validacion.tipo_compra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<!----------    Beneficios         -------------->
			<div v-show="btn_producto" v-for="(item, index) in compras.componentes" :key="index">
				<b-form>
					<div class="d-flex  justify-content-between">

						<h4>Producto: {{ index + 1 }}</h4>
						<b-button variant="danger"
								  @click="eliminarProducto(index)"
								  style="color: white">
							<v-icon style="color: white"> mdi-delete </v-icon>
						</b-button>
					</div>
					<b-form-group label="*Producto"
								  label-for="producto"
								  @submit.stop.prevent="handleSubmit">
						<v-autocomplete id="producto"
										:state="validacion.componentes[index].producto.estado"
										v-model="item.producto"
										type="text"
										placeholder="Ingrese un Producto"
										invalid-feedback="Complete este campo"
										required
										solo
										filled
										:items="op_productos">
						</v-autocomplete>
						<b-form-invalid-feedback id="producto-live-feedback">
							{{ validacion.componentes[index].producto.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Cantidad Comprada" label-for="cantidad">
						<b-form-input id="cantidad"
									  :state="validacion.componentes[index].cantidad.estado"
									  v-model="item.cantidad"
									  type="number"
									  placeholder="*Ingrese la cantidad"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio);">
						</b-form-input>
						<b-form-invalid-feedback id="cantidad-live-feedback">
							{{ validacion.componentes[index].cantidad.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<b-form-group label="*Costo Unitario" label-for="precio">
						<span style="display:inline-block;width:3%;">$</span>
						<b-form-input id="precio"
									  :state="validacion.componentes[index].precio.estado"
									  v-model="item.precio"
									  type="number"
									  style="width:97%;display:inline-block;"
									  placeholder="*Ingrese el precio"
									  invalid-feedback="Complete este campo"
									  required
									  v-on:keyup="sumar(item.cantidad, item.precio, index)">
						</b-form-input>
						<b-form-invalid-feedback id="precio-live-feedback">
							{{ validacion.componentes[index].precio.mensaje }}
						</b-form-invalid-feedback>
					</b-form-group>

					<span>Total Producto: $</span> {{item.cantidad * item.precio}}
				</b-form>
				<b-button class="my-3"
						  variant="success"
						  @click="sumarProductos()"
						  style="color: white">+</b-button>
			</div>

			<br><br><span style="text-align:center;width:100%;">Total compra: ${{totalpago}}</span>

		</b-form>

		<b-button class="mt-2" :disabled="botonalta" variant="success" block @click="postcompra()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
    import axios from "axios";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				compras: {componentes:[]},
				data: {},
				list_productos: {},
                productoelegido: {},
				text: "",
				totalprod: [0],
                totalpago: 0,
				numero:-1,
				op_productos: [],
				listaproduct: [],
				botonalta: false,
                tipos_productos: [
                    { value: null, text: "Elija un tipo de producto", disabled: true },
                    { value: "Pollo", text: "Pollo" },
                    { value: "Congelados", text: "Congelado" }
                ],
                listacompra: [],
				validacion: {
					fecharealizacion: { estado: null, mensaje: "" },
                    tipo_compra: { estado: null, mensaje: "" },
                    componentes: [],
				},
			};
		},

		methods: {

			async postcompra() {
                this.botonalta = true;
				let comprasAPI = new APIControler();
				comprasAPI.apiUrl.pathname = "compras/";
				let respuesta = await comprasAPI.postData(this.compras);
                if (respuesta !== undefined) {
                    this.botonalta = false
                }
				console.log(compras);
				console.log(compras.fecharealizacion);
				this.compras.componentes.forEach(element => {
					console.log("Longitud");
					this.actualizarStockProducto(element.producto.split('/')[4], element.cantidad);
				})
				this.cargarFeedback(respuesta);
				this.updateTable();
			},
			getProducto(id) {
				let producto = this.listaproduct.find(s => {
					return s.id_producto == id
				})
				return producto;
			},
			async actualizarStockProducto(id, stock) {
				console.log(this.getProducto(id));
				let productofinal = this.getProducto(id);
				this.productoelegido.id_producto = productofinal.id_producto
				this.productoelegido.nombre = productofinal.nombre
				this.productoelegido.tipo_producto = productofinal.tipo_producto
				this.productoelegido.letra = productofinal.letra
				this.productoelegido.numero_producto = productofinal.numero_producto
				this.productoelegido.codigo_unico = productofinal.codigo_unico
				this.productoelegido.stocktotal = parseFloat(productofinal.stocktotal) + parseFloat(stock)
				this.productoelegido.precioref = productofinal.precioref
				console.log(this.productoelegido)
				await axios
					.put(
						"http://93.188.162.174:8081/productos/" +
						this.productoelegido.id_producto +
						"/",
						this.productoelegido
					);
			},
			sumar(valor1, valor2, index) {
				var total = 0;
				valor1 = parseFloat(valor1);
                valor2 = parseFloat(valor2);// Convertir el valor a un entero (número).
				// Aquí valido si hay un valor previo, si no hay datos, le pongo un cero "0".
				//total = (total == null || total == undefined || total == "") ? 0 : total;
				/* Esta es la suma. */
                total = (parseFloat(valor1) * parseFloat(valor2));
				// Colocar el resultado de la suma en el control "span".
				(this.totalprod)[index] = total;
				this.totalprod.push(0);
				this.totalprod.pop();
				this.calctotalpago();
			},
            calctotalpago() {
                let total = 0;
                for (var i = 0; i < this.compras.componentes.length; i++) {
                    if (!isNaN((this.compras.componentes)[i].precio) && !isNaN((this.compras.componentes)[i].cantidad)) {
                        total = total + ((this.compras.componentes)[i].precio * (this.compras.componentes)[i].cantidad);
                    }
                }
                console.log(total);
                this.totalpago = total;
            },
			handleInput() {
				var today = new Date();
				today.setDate(today.getDate());
				var date = today.getFullYear() + "-" + ('0' + (today.getMonth() + 1)).slice(-2) + "-" + ('0' + today.getDate()).slice(-2);
				this.compras.fecharealizacion = date;
                this.compras.tipo_compra = 'Pollo';
				console.log(String(date));
			},
			async resetForm() {
				this.compras.fecharealizacion = "";
				this.compras.componentes = null;
			},
			//Obtengo los productos
			async getProductos() {
				let productosAPI = new APIControler();
				productosAPI.apiUrl.pathname = "productos/";
				this.listaproduct = await productosAPI.getData();
                let compraAPI = new APIControler();
				compraAPI.apiUrl.pathname = "compras/";
                this.listacompra = await compraAPI.getData();
				let option
				this.listaproduct.forEach((element) => {
					option = {};
					option.value =
						"http://93.188.162.174:8081/productos/" + element.id_producto + "/";
                    option.text = element.nombre + ' - Stock: ' + element.stocktotal + ' - ' + this.getFechaPrecio(element.id_producto);
					console.log(option);
                    if (element.tipo_producto == this.compras.tipo_compra) {
                        this.op_productos.push(option);
                    }
				});
			},
            cambiodetipo() {
				this.op_productos = [];
                this.getProductos();
            },
			getFechaPrecio(id)
			{
                let fecha = "Nunca se compro";
				let fechaelemento = new Date(2000,1,1);
				let ultimoprecio = 0;
				console.log(this.listacompra);
				this.listacompra.forEach((element) => {
					fechaelemento = new Date(element.fecharealizacion)
					element.componentes.forEach((compon) =>
					{
                        if (compon.producto.split('/')[4] == id)
						{
							ultimoprecio = compon.precio;
                            fecha = (fechaelemento.getUTCDate()) + "/" + (fechaelemento.getMonth() + 1) + "/" + (fechaelemento.getUTCFullYear());
						}
					});
				});
				return ('Ult. Cost.:' + ultimoprecio + ' - ' + fecha);
			},
            async sumarProductos() {
				this.btn_producto = true;
				this.numero = this.numero + 1;
				// this.resetFormAdh();
				// this.cantidad=this.cantidad +1;
				this.compras.componentes.push({
					cantidad: 0,
					producto: null,
					precio: 0,
				});
                this.validacion.componentes.push({
					cantidad: { estado: null, mensaje: "" },
					producto: { estado: null, mensaje: "" },
                    precio: { estado: null, mensaje: "" },
				});
            },
			async eliminarProducto(index) {
                if (this.compras.componentes.length == 1) {
					alert("Tiene que haber al menos 1 producto");
				} else {
					this.compras.componentes.splice(index, 1)
					this.validacion.componentes.splice(index, 1)
					this.totalprod.splice(index);
					this.numero = this.numero - 1;
                    this.calctotalpago();
				}
            },
			cargarFeedback(respuesta) {
				let valido;
				if (!respuesta) respuesta = {};
				for (let key in this.validacion) {
					valido = !respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					if (!valido) this.validacion[key].mensaje = respuesta[key][0];
				}
                this.cargarFeedbackcomponentes(respuesta)
			},
            cargarFeedbackcomponentes(respuesta) {
				let valido;
                let ok=false
                if (!respuesta.componentes) ok=true;
                this.validacion.componentes.forEach((b,index )=> {
                    for (let key in b) {
                        valido = ok || !respuesta.componentes[index].hasOwnProperty(key);
                        b[key].estado = valido;
                        console.log(key);
                        if (!valido) b[key].mensaje = respuesta.componentes[index][key][0];
                    }
                });
				
			},
		},
		beforeMount() {
			this.getProductos()
			this.sumarProductos()
			this.handleInput()
		},
		/*
  computed: {
      validation() {
        return this.text.length > 0 ? true : false;
      },
    }
  */
	};
</script>

<style></style>
